<template>
  <div class="form_body">
    <!--  -->
    <div class="pulgins">
      <div class="pulgins_title"></div>
      <Row>
        <Col span="8">
          <div class="pilgin_init input" @mousedown="handleMove">
            <div class="pilgin_title"></div>
          </div>
        </Col>
        <Col span="8">
          <div class="pilgin_init label" @mousedown="handleMove">
            <div class="pilgin_title"></div>
          </div>
        </Col>
        <Col span="8">
          <div class="pilgin_init select" @mousedown="handleMove">
            <div class="pilgin_title"></div>
          </div>
        </Col>
      </Row>
    </div>
    <!--  -->
    <div class="dropbox">
      <div class="pulgins_title" ref="dropbox"></div>
      <GridLayout
        :layout="testLayout"
        :col-width="140"
        :row-height="40"
        :is-draggable="true"
        :is-resizable="true"
        :vertical-compact="true"
        :margin="[1, 1]"
        :use-css-transforms="true"
      >
        <GridItem
          v-for="item in testLayout"
          :key="item.i"
          :x="item.x"
          :y="item.y"
          :w="item.w"
          :h="item.h"
          :i="item.i"
          @moved="movedEvent"
          class="griditem"
        >
          
        </GridItem>
      </GridLayout>
    </div>
    <!--  -->
    <div class="formlist">
      <div class="pulgins_title"></div>
    </div>
  </div>
</template>

<script>
import vueGridLayout from "vue-grid-layout";
const matedata = [
  {
    i: "0", //  
    h: 4, //    
    w: 7, //    
    x: 2, // x  
    y: 0, // y 
    minW: 5, //   wminW minW
    minH: 3, // 
    maxW: 8, // wmaxWwmaxW
    maxH: 6, // 
    isDraggable: true, //    
    isResizable: true, //    
    static: false, //    
    dragIgnoreFrom: "", // css  //  
    dragAllowFrom: "", // css   // 
    resizeIgnoreFrom: "", //css  //
  },
  {
    h: 1,
    i: "1",
    w: 1,
    x: 0,
    y: 1,
  },
  {
    h: 1,
    i: "2",
    w: 1,
    x: 0,
    y: 2,
  },
  {
    h: 1,
    i: "3",
    w: 1,
    x: 0,
    y: 3,
  },
];
export default {
  components: {
    GridLayout: vueGridLayout.GridLayout, //
    GridItem: vueGridLayout.GridItem, // 
  },
  data() {
    return {
      testLayout: [],
    };
  },
  methods: {
    handleMove(el) {
      console.log(111)
      if (el.which == 1) {
        let moveItem = window.document.createElement("div");
        moveItem.style.position = "absolute";
        moveItem.style.left = el.pageX - 50 + "px";
        moveItem.style.top = el.pageY - 50 + "px";
        moveItem.innerHTML = el.target.innerHTML;
        moveItem.className = el.target.className + " move_style";
        window.document.body.appendChild(moveItem);
        document.onmousemove = (mouse) => {
          moveItem.style.left = mouse.pageX - 50 + "px";
          moveItem.style.top = mouse.pageY - 50 + "px";
        };
        document.onmouseup = (mouse) => {
          let dropdetails = this.getElemDis(this.$refs.dropbox);
          console.log(dropdetails);
          const mouseX = mouse.pageX;
          const mouseY = mouse.pageY;
            let controlX = Number.parseInt(
              (mouse.pageX - dropdetails.left) /
                (this.$refs.dropbox.offsetWidth / 8)
            );
            let controlY = Number.parseInt(
              (mouse.pageY - dropdetails.top) / 120
            );
            this.$nextTick(() => {
              let controlParams = {
                controlX: controlX,
                controlY: controlY,
              };
              this.controlInit(controlParams);
            });
            moveItem.remove();
        };
      }
    },
      // 
      movedEvent(i, newX, newY) {  //
        // let movedControl = this.controlLayout.filter((controlLayoutObj) => {
        //   return Number.parseInt(controlLayoutObj.i) == Number.parseInt(i)
        // })
        // transform
        // console.log("MOVED i=" + i + ", X=" + newX + ", Y=" + newY);
      },
    // 
    controlInit(data) {
      this.testLayout.push({
        x: data.controlX,
        y: data.controlY,
        w: 1,
        h: 1,
        i: new Date().getTime(),
      });
    },
    // 
    getElemDis(el) {
      var tp = document.documentElement.clientTop,
        lt = document.documentElement.clientLeft,
        rect = el.getBoundingClientRect();
      return {
        top: rect.top - tp,
        right: rect.right - lt,
        bottom: rect.bottom - tp,
        left: rect.left - lt,
      };
    },
  },
};
</script>

<style>
.form_body {
  width: 100%;
  height: 752px;
  background: #fff;
  display: flex;
}
.pulgins,
.formlist {
  width: 300px;
  background: #fff;
  box-sizing: border-box;
  box-shadow: 1px 0 5px rgb(216, 216, 216);
  display: inline-block;
  height: 100%;
}
.dropbox {
  flex: 1;
  display: inline-block;
  height: 100%;
}
.pilgin_init {
  height: 90px;
  text-align: center;
  box-sizing: border-box;
  border: 1px solid #eee;
  cursor: move;
}
.pilgin_title {
  margin-top: 67px;
  width: 100%;
  text-align: center;
}
.pulgins_title {
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-bottom: 1px solid #eee;
}
.move_style {
  border: none !important;
}
.input {
  background: url("../assets/formIcon/input.svg") center center no-repeat;
  background-size: auto 40%;
}
.label {
  background: url("../assets/formIcon/label.svg") center center no-repeat;
  background-size: auto 40%;
}
.select {
  background: url("../assets/formIcon/select.svg") center center no-repeat;
  background-size: auto 40%;
}
.griditem {
  border: 1px dashed #808695;
}
</style>